.smallCard{
    min-width: 600px;
    height: 400px;
    border-radius: 40px;
    background-repeat: no-repeat;
    background-position: left bottom;
    background-size: contain;
    padding: 50px 50px 55px;
    margin-right: 15px;
    margin-bottom: 30px;
    border-radius: 30px;
}
.smallCardH1{
    font-size: 50px;
    letter-spacing: 0px;
    font-weight: 900;
    color: white;
}
.smallCardH3{
    font-size: 40px;
    letter-spacing: 0px;
    font-weight: 400;
    color: white;
}

/* Media Queries */
@media (max-width: 1200px) {
    .smallCard {
        min-width: 80%; /*      */
        height: auto; /*    */
        padding: 40px; /*   */
        margin-right: auto; /*   */
        margin-left: auto; /*   */
    }

    .smallCardH1 {
        font-size: 40px; /*    */
    }

    .smallCardH3 {
        font-size: 30px; /*    */
    }
}

@media (max-width: 768px) {
    .smallCard {
        min-width: 100%; /*      */
        padding: 30px; /*    */
        margin-bottom: 10px; /*    */
        min-height: 400px !important;
    }

    .smallCardH1 {
        font-size: 45px; /*     */
    }

    .smallCardH3 {
        font-size: 25px; /*     */
    }
}

@media (max-width: 480px) {
    .smallCard {
        min-width: 100%; /*      */
        padding: 20px; /*    */
        margin-bottom: 15px; /*    */
    }

    .smallCardH1 {
        font-size: 30px; /*     */
    }

    .smallCardH3 {
        font-size: 20px; /*     */
    }
}