.item{
    display: flex;
    align-items: center;
    gap: 50px;
    width: 100%;
    height: 200px;
    border: 0.5px solid #e6e4e4;
    padding-right: 20px;
    position: relative;
}
.item a{
    color: black !important;
}
.imgItem{
    width: 200px;
    height: 200px;
    background: none;
    z-index: -3;
}
.ItSelectedList h5{
    font-size: 20px;
    font-weight: 200;
}
.ItSelectedList h3{
    cursor: pointer;
    font-size: 30px;
    transition: all 0.4s ease-in-out;
}
.close{
    position: absolute;
    right: 10px;
}
.ItSelectedList .close:hover{
    color: #01e281;
    transform: rotateY(180deg);
}

/* Media Queries */
@media (max-width: 1200px) {
    .item {
        height: 180px; /*    */
        gap: 30px; /*     */
    }

    .imgItem {
        width: 180px; /*    */
        height: 180px; /*    */
    }

    .ItSelectedList h5 {
        font-size: 18px; /*    h5 */
    }

    .ItSelectedList h3 {
        font-size: 28px; /*    h3 */
    }
}

@media (max-width: 768px) {
    .item {
        flex-direction: column; /*      */
        height: auto; /*    */
        gap: 20px; /*      */
    }

    .imgItem {
        width: 150px; /*    */
        height: 150px; /*    */
    }

    .ItSelectedList h5 {
        font-size: 16px; /*    h5  */
    }

    .ItSelectedList h3 {
        font-size: 24px; /*    h3  */
    }

    .close {
        right: 5px; /*     */
    }
}

@media (max-width: 480px) {
    .item {
        padding-right: 10px; /*    */
        gap: 10px; /*      */
    }

    .imgItem {
        width: 120px; /*    */
        height: 120px; /*    */
    }

    .ItSelectedList h5 {
        font-size: 14px; /*    h5  */
    }

    .ItSelectedList h3 {
        font-size: 22px; /*    h3  */
    }

    .close {
        right: 2px; /*      */
    }
}