.LeftTableCheckout{
    display: flex;
    align-items: center;
    gap: 80px;
    padding: 10px;
    padding-top: 30px;
    background-color: #f1f2f6;
    width: 50%;
    margin: 100px;
    border-radius: 40px;
    margin-left: 50px !important;
    flex-direction: column;
}

.bigLabel{
    display: flex;
    align-items: center;
    color: #7a7a7a;
    transform: translate(30px, 23px) !important;
    margin-bottom: 40px;
    font-weight: 900;
    width: 286px !important;
}
.textArea{
    border: 1px solid #d1d0d0;
    border-radius: 40px;
    padding: 30px;
    margin-bottom: 50px;
    
}

/* Media Queries */
@media (max-width: 1200px) {
    .LeftTableCheckout {
        width: 70%; /*      */
        margin: 50px auto; /*   */
        padding: 20px; /*   */
    }

    .bigLabel {
        width: auto; /*    */
        transform: translate(20px, 20px) !important; /*   */
        margin-bottom: 30px; /*    */
    }

    .textArea {
        padding: 20px; /*    */
        margin-bottom: 40px; /*    */
    }
}

@media (max-width: 768px) {
    .LeftTableCheckout {
        width: 90%; /*      */
        margin: 30px auto; /*   */
        padding: 15px; /*   */
        gap: 40px; /*     */
    }

    .bigLabel {
        transform: translate(10px, 15px) !important; /*   */
        margin-bottom: 20px; /*    */
        font-size: 18px; /*    */
    }

    .textArea {
        padding: 15px; /*    */
        margin-bottom: 30px; /*    */
    }
}

@media (max-width: 480px) {
    .LeftTableCheckout {
        width: 95%; /*      */
        margin: 20px auto; /*   */
        padding: 10px; /*   */
        gap: 20px; /*     */
    }

    .bigLabel {
        transform: translate(5px, 10px) !important; /*   */
        margin-bottom: 15px; /*    */
        font-size: 16px; /*     */
    }

    .textArea {
        padding: 10px; /*    */
        margin-bottom: 20px; /*    */
    }
}