.linkFooter{
    display: flex;
    align-items: center;
    gap: 15px;
    font-size: 25px;
    color: white;
    transition: all 0.2s ease-in;
    cursor: pointer;
}
.linkFooter:hover{
    transform: translateX(10px);
    color: #01e281;
}
.linkFooter .iconLink{
    color: #01e281;
    border-radius: 50%;
    font-size: 50px;
    padding: 10px;
    background-color: #1C3E56;
}
@media (min-width: 1024px) and (max-width: 1823px)
{
    .linkFooter {
        font-size: 16px; /*    */
        gap: 10px; /*     */
    }

    .linkFooter .iconLink {
        font-size: 45px; /*    */
        padding: 8px; /*   */
    }
}
/* Media Queries */
@media (min-width: 992px) and (max-width: 1023px)
    {
    .linkFooter {
        font-size: 16px; /*    */
        gap: 10px; /*     */
    }

    .linkFooter .iconLink {
        font-size: 45px; /*    */
        padding: 8px; /*   */
    }
}

@media (max-width: 768px) {
    .linkFooter {
        font-size: 20px; /*     */
        gap: 8px; /*     */
    }

    .linkFooter .iconLink {
        font-size: 40px; /*    */
        padding: 6px; /*   */
    }
}

@media (min-width: 768px) and (max-width: 991px)  {
    .linkFooter {
        flex-direction: column; /*     */
        font-size: 18px; /*     */
        gap: 5px; /*     */
    }

    .linkFooter .iconLink {
        font-size: 35px; /*    */
        padding: 5px; /*   */
    }
}