.ItSelectedList{
    display: flex;
    flex-direction: column;
    width: 650px;
    min-height: 300px;
    z-index: 376;
    background-color: white;
    padding: 20px;
    position: absolute;
    border-radius: 30px;
    top: 0;
    transform: translate(420px, 210px);
    transition: all 1.5s ease-in-out;
}
.bottomField{
    width: 100%;
    height: 100px;
    display: flex;
    align-items: center;
}
.details{
    width: 50%;
    height: 100%;
    display: flex;
    padding-left: 30px;
    align-items: center;
    justify-content: center;
    border: 0.5px solid #e6e4e4;
}
.details a{
    color: black !important;

}
.details h3{
    font-size: 20px !important;
}
.details:first-child{
    border-radius: 0px 0px 0px 20px;
}
.details:last-child{
    border-radius: 0px 0px 20px 0px;
}
.cartIcon,
.coinIcon{
    font-size: 20px;
}
.FooterList{
    width: 100%;
    height: 80px;
    font-size: 15px;
    font-weight: 900;
    background-color: #01e281;
    padding-top: 10px;
    color: #173850;
    border-radius: 0px 0px 20px 20px;
    text-align: center;
}
.after{
    position: absolute;
    top: -36px;
    right: 20px;
    font-size: 40px;
    color: #01e281;
}
/* Media Queries */
@media (max-width: 1200px) {
    .ItSelectedList {
        width: 80%; /*      */
        transform: translate(20px, 210px); /*   */
        padding: 15px; /*   */
    }

    .details {
        width: 100%; /*      */
        padding-left: 15px; /*    */
    }

    .details h3 {
        font-size: 18px; /*    */
    }

    .cartIcon,
    .coinIcon {
        font-size: 18px; /*    */
    }
}

@media (max-width: 768px) {
    .ItSelectedList {
        width: 90%; /*      */
        transform: translate(10px, 150px); /*   */
        min-height: auto; /*    */
        padding: 10px; /*   */
    }

    .bottomField {
        height: auto; /*    */
        flex-direction: column; /*     */
    }

    .details {
        width: 100%; /*      */
        padding-left: 10px; /*    */
        justify-content: flex-start; /*     */
    }

    .details h3 {
        font-size: 16px; /*     */
    }

    .FooterList {
        height: auto; /*    */
        font-size: 14px; /*    */
    }

    .after {
        font-size: 30px; /*    */
        right: 10px; /*   */
    }
}

@media (max-width: 480px) {
    .ItSelectedList {
        width: 95%; /*      */
        transform: translate(5px, 100px); /*    */
        padding: 5px; /*    */
    }

    .bottomField {
        height: auto; /*    */
        flex-direction: column; /*     */
    }

    .details {
        padding-left: 5px; /*     */
        justify-content: flex-start; /*     */
    }

    .details h3 {
        font-size: 14px; /*     */
    }

    .FooterList {
        height: auto; /*    */
        font-size: 12px; /*     */
    }

    .after {
        font-size: 24px; /*     */
        right: 5px; /*    */
    }
}