
.FAQ {
  width: 100%;
  height: auto; /*      */
  padding-bottom: 50px; /*      */
}

.colorIcon {
  color: white;
  font-size: 4vw; /*       */
}

.rightFaq {
  width: 90%;
  max-width: 30%; /*       */
  height: auto; /*     */
  display: flex;
  float: none; /*  float       */
  padding: 4vh 2vw; /*      */
  flex-direction: column;
  position: static; /*  position: absolute   */
  align-items: center;
  gap: 2vh; /*    */
  background-color: #122d40;
  border-radius: 50px;
  margin: 0 auto; /*     */
  top: 0;
  right: 0;
}

.labelIn {
  transform: translate(-15vw, 2vh); /*      */
}

.Send {
  width: 40vw; /*      */
  height: 7vh; /*      */
  transform: translate(10vw, 10vh); /*      */
}

.colorQues {
  color: #122d40;
  width: 10vw; /*      */
  height: 10vw;
}

.question {
  width: 100%;
  border: 0.5px solid #dddddd;
  padding: 2vh 2vw; /*      */
  border-radius: 30px;
  transition: all 1.0s ease-in-out;
}

.question:hover {
  background: linear-gradient(45deg, #122d40, transparent);
  color: #01e281;
  transform: rotateX(360deg);
}

/*   */
@media (min-width: 1200px) {
  .boxForm {
      width: 60%;
      height: 100vh;
      padding: 5vh 5vw; /*      */
      margin-top: 10vh;
  }

  .inputs {
      margin-top: 10vh;
  }

  .btnSend {
      width: 20vw;
      height: 7vh;
      text-align: center;
      font-weight: 900 !important;
  }
}

/*   */
@media (min-width: 992px) and (max-width: 1199px) {
  .boxForm {
      width: 80%;
      height: auto;
      padding: 4vh 4vw;
      margin-top: 5vh;
  }

  .inputs {
      margin-top: 5vh;
  }

  .btnSend {
      width: 20vw;
      height: 6vh;
  }
}

/*   */
@media (min-width: 768px) and (max-width: 991px) {
  .boxForm {
      width: 90%;
      height: auto;
      padding: 3vh 3vw;
      margin-top: 3vh;
  }

  .inputs {
      flex-direction: column;
      margin-top: 3vh;
  }

  .btnSend {
      width: 25vw;
      height: 5vh;
  }

  .rightFaq {
      width: 100%;
      height: auto;
      padding: 3vh 2vw;
      margin-top: 5vh;
      max-width: none; /*     */
  }

  .Send {
      width: 50vw;
      transform: translate(0, 5vh); /*     */
  }

  .labelIn {
      transform: translate(-10vw, 2vh);
  }

  .colorQues {
      width: 15vw;
      height: 15vw;
  }

  .colorIcon {
      font-size: 5vw; /*      */
  }
}

/*    */
@media screen and (max-width: 767px) {
  .boxForm {
      width: 100%;
      padding: 2vh 2vw;
      margin-top: 2vh;
  }

  .inputs {
      flex-direction: column;
      margin-top: 2vh;
  }

  .btnSend {
      width: 35vw;
      height: 4vh;
  }

  .rightFaq {
      width: 100%;
      padding: 2vh 1vw;
      margin-top: 3vh;
  }

  .Send {
      width: 70vw;
      transform: translate(0, 2vh);
  }

  .labelIn {
      transform: translate(-8vw, 2vh);
  }

  .colorQues {
      width: 20vw;
      height: 20vw;
  }

  .colorIcon {
      font-size: 6vw;
  }

  .question {
      padding: 2vh 2vw;
  }
}
