.Checkout{
    width: 100%;
    height: 350vh;
}

.sectionCode{
    width: 93%;
    height: 150px;
    margin-top: 150px;
    margin-left: 70px;
    background-color: #f9f9f9;
    position: relative;
    padding-top: 40px;
    padding-left: 150px;
}
.question h2{
    font-size: 30px;
    transform: translateY(2px);
    transition: all 0.5s ease-in-out;
    cursor: pointer;
}
.question h2:hover{
    color: #01e281;
}
.codeSquare{
    position: absolute;
    left: -20px;
    top: 35px;
    color: #357db1;
    font-size: 30px;
    width: 80px;
    height: 80px;
    background-color: white;
    box-shadow: 2px 2px 2px 10px #f9f9f9;
}

/* Media Queries */
@media (max-width: 1200px) {
    .sectionCode {
        margin-left: 50px; /*      */
        padding-left: 100px; /*    */
        height: auto; /*    */
        padding-top: 20px; /*    */
    }

    .question h2 {
        font-size: 28px; /*    */
    }

    .codeSquare {
        width: 70px; /*    */
        height: 70px; /*    */
        font-size: 28px; /*      */
        top: 30px; /*   */
    }
}

@media (max-width: 768px) {
    .sectionCode {
        margin-left: 30px; /*    */
        padding-left: 50px; /*     */
        height: auto; /*    */
        padding-top: 15px; /*     */
    }

    .question h2 {
        font-size: 24px; /*     */
    }

    .codeSquare {
        width: 60px; /*     */
        height: 60px; /*     */
        font-size: 24px; /*       */
        top: 25px; /*    */
    }
}

@media (max-width: 480px) {
    .sectionCode {
        margin-left: 10px; /*       */
        padding-left: 20px; /*       */
        height: auto; /*    */
        padding-top: 10px; /*       */
    }

    .question h2 {
        font-size: 20px; /*      */
    }

    .codeSquare {
        width: 50px; /*      */
        height: 50px; /*      */
        font-size: 20px; /*        */
        top: 20px; /*     */
    }
}